.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.webp header {
  background-image: url("/build/img/header.webp");
  background-position: center;
  background-repeat: repeat;
  background-size: cover;
}

.no-webp header {
  background-image: url("/build/img/header.jpg");
  background-position: center center;
  background-repeat: repeat;
  background-size: cover;
}

.contenido-header {
  display: flex;
  justify-content: center;
  background: rgb(23, 61, 17);
  background: radial-gradient(circle, rgb(23, 61, 17) 0%, rgb(44, 93, 25) 8%, rgb(31, 73, 20) 15%, rgba(50, 129, 42, 0) 100%);
}
.contenido-header .titule {
  font-size: 5rem;
  font-weight: 600;
  padding: 1rem;
  margin: 0;
  -webkit-text-stroke: 4px #af6907;
  color: #E08709;
}
@media (min-width: 768px) {
  .contenido-header .titule {
    font-size: 8rem;
    font-weight: bold;
  }
}
.contenido-header .titule span {
  font-size: 2rem;
  font-weight: 600;
  color: #E08709;
  -webkit-text-stroke: 1px #b96f07;
}

body {
  background-color: #ECE7B4;
}
body.negro {
  background-color: rgba(0, 0, 0, 0.8);
}

.contenido-main .titule {
  font-size: 2.8rem;
  font-weight: 200;
  padding: 1rem;
  margin: 0;
  -webkit-text-stroke: 2px #b96f07;
  color: #E08709;
}
@media (min-width: 768px) {
  .contenido-main .titule {
    font-size: 6rem;
    font-weight: bold;
  }
}
.contenido-main .unidades {
  display: grid;
  grid-template-columns: 3;
  gap: 2rem;
}
.contenido-main .unidades .contenedor-unidad {
  background-color: #406343;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .contenido-main .unidades .contenedor-unidad {
    width: auto;
  }
}
.contenido-main .unidades .contenedor-unidad:hover {
  background-color: #2c442e;
  cursor: pointer;
}
.contenido-main .unidades .contenedor-unidad button {
  margin: 0;
  border: none;
  padding: 1rem 5rem;
  border-radius: 1rem;
  background-color: #E08709;
  color: #FFFFFF;
}
.contenido-main .modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.contenido-main .modal .teoria {
  overflow: scroll;
  width: 80%;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: transform, opacity;
  transition-duration: 400ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
  border-radius: 1rem;
}
.contenido-main .modal .teoria::-webkit-scrollbar {
  display: none;
}
.contenido-main .modal .teoria.animar {
  position: relative;
  top: 35%;
  opacity: 1;
}
.contenido-main .modal .teoria.cerrar {
  transform: translateY(-500%);
  opacity: 0;
}
.contenido-main .modal .teoria .cerrar-modal {
  background-color: #E08709;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .contenido-main .modal .teoria .cerrar-modal {
    width: auto;
  }
}
.contenido-main .modal .teoria .cerrar-modal:hover {
  background-color: #af6907;
  cursor: pointer;
}